(function () {

/////////////////////////////////////////////////////////////////////////////////////
//                                                                                 //
// packages/aldeed:http/http-extras-common.js                                      //
//                                                                                 //
/////////////////////////////////////////////////////////////////////////////////////
                                                                                   //
makeErrorByStatus = function(statusCode, content) {                                // 1
  var MAX_LENGTH = 500; // if you change this, also change the appropriate test    // 2
                                                                                   // 3
  var truncate = function(str, length) {                                           // 4
    return str.length > length ? str.slice(0, length) + '...' : str;               // 5
  };                                                                               // 6
                                                                                   // 7
  var message = "failed [" + statusCode + "]";                                     // 8
  if (content) {                                                                   // 9
    try {                                                                          // 10
      message += " " + truncate(content.replace(/\n/g, " "), MAX_LENGTH);          // 11
    }                                                                              // 12
    catch (error) {                                                                // 13
      message += " Response is not of type String ";                               // 14
    }                                                                              // 15
  }                                                                                // 16
                                                                                   // 17
  return new Error(message);                                                       // 18
};                                                                                 // 19
                                                                                   // 20
encodeParams = function(params) {                                                  // 21
  var buf = [];                                                                    // 22
  _.each(params, function(value, key) {                                            // 23
    if (buf.length)                                                                // 24
      buf.push('&');                                                               // 25
    buf.push(encodeString(key), '=', encodeString(value));                         // 26
  });                                                                              // 27
  return buf.join('').replace(/%20/g, '+');                                        // 28
};                                                                                 // 29
                                                                                   // 30
encodeString = function(str) {                                                     // 31
  return encodeURIComponent(str).replace(/[!'()]/g, escape).replace(/\*/g, "%2A"); // 32
};                                                                                 // 33
                                                                                   // 34
buildUrl = function(before_qmark, from_qmark, opt_query, opt_params) {             // 35
  var url_without_query = before_qmark;                                            // 36
  var query = from_qmark ? from_qmark.slice(1) : null;                             // 37
                                                                                   // 38
  if (typeof opt_query === "string")                                               // 39
    query = String(opt_query);                                                     // 40
                                                                                   // 41
  if (opt_params) {                                                                // 42
    query = query || "";                                                           // 43
    var prms = encodeParams(opt_params);                                           // 44
    if (query && prms)                                                             // 45
      query += '&';                                                                // 46
    query += prms;                                                                 // 47
  }                                                                                // 48
                                                                                   // 49
  var url = url_without_query;                                                     // 50
  if (query !== null)                                                              // 51
    url += ("?"+query);                                                            // 52
                                                                                   // 53
  return url;                                                                      // 54
};                                                                                 // 55
                                                                                   // 56
// Fill in `response.data` if the content-type is JSON.                            // 57
populateData = function(response) {                                                // 58
  // Read Content-Type header, up to a ';' if there is one.                        // 59
  // A typical header might be "application/json; charset=utf-8"                   // 60
  // or just "application/json".                                                   // 61
  var contentType = (response.headers['content-type'] || ';').split(';')[0];       // 62
                                                                                   // 63
  // Only try to parse data as JSON if server sets correct content type.           // 64
  if (_.include(['application/json', 'text/javascript'], contentType)) {           // 65
    try {                                                                          // 66
      response.data = JSON.parse(response.content);                                // 67
    } catch (err) {                                                                // 68
      response.data = null;                                                        // 69
    }                                                                              // 70
  } else {                                                                         // 71
    response.data = null;                                                          // 72
  }                                                                                // 73
};                                                                                 // 74
                                                                                   // 75
/////////////////////////////////////////////////////////////////////////////////////

}).call(this);






(function () {

/////////////////////////////////////////////////////////////////////////////////////
//                                                                                 //
// packages/aldeed:http/http-extras-client.js                                      //
//                                                                                 //
/////////////////////////////////////////////////////////////////////////////////////
                                                                                   //
HTTP.call = function(method, url, options, callback) {                             // 1
                                                                                   // 2
  ////////// Process arguments //////////                                          // 3
                                                                                   // 4
  if (! callback && typeof options === "function") {                               // 5
    // support (method, url, callback) argument list                               // 6
    callback = options;                                                            // 7
    options = null;                                                                // 8
  }                                                                                // 9
                                                                                   // 10
  options = options || {};                                                         // 11
                                                                                   // 12
  if (typeof callback !== "function")                                              // 13
    throw new Error(                                                               // 14
      "Can't make a blocking HTTP call from the client; callback required.");      // 15
                                                                                   // 16
  method = (method || "").toUpperCase();                                           // 17
                                                                                   // 18
  var headers = {};                                                                // 19
                                                                                   // 20
  var content = options.content;                                                   // 21
  if (options.data) {                                                              // 22
    content = JSON.stringify(options.data);                                        // 23
    headers['Content-Type'] = 'application/json';                                  // 24
  }                                                                                // 25
                                                                                   // 26
  var params_for_url, params_for_body;                                             // 27
  if (content || method === "GET" || method === "HEAD")                            // 28
    params_for_url = options.params;                                               // 29
  else                                                                             // 30
    params_for_body = options.params;                                              // 31
                                                                                   // 32
  var query_match = /^(.*?)(\?.*)?$/.exec(url);                                    // 33
  url = buildUrl(query_match[1], query_match[2],                                   // 34
                 options.query, params_for_url);                                   // 35
                                                                                   // 36
  if (options.followRedirects === false)                                           // 37
    throw new Error("Option followRedirects:false not supported on client.");      // 38
                                                                                   // 39
  var username, password;                                                          // 40
  if (options.auth) {                                                              // 41
    var colonLoc = options.auth.indexOf(':');                                      // 42
    if (colonLoc < 0)                                                              // 43
      throw new Error('auth option should be of the form "username:password"');    // 44
    username = options.auth.substring(0, colonLoc);                                // 45
    password = options.auth.substring(colonLoc+1);                                 // 46
  }                                                                                // 47
                                                                                   // 48
  if (params_for_body) {                                                           // 49
    content = encodeParams(params_for_body);                                       // 50
  }                                                                                // 51
                                                                                   // 52
  _.extend(headers, options.headers || {});                                        // 53
                                                                                   // 54
  ////////// Callback wrapping //////////                                          // 55
                                                                                   // 56
  // wrap callback to add a 'response' property on an error, in case               // 57
  // we have both (http 4xx/5xx error, which has a response payload)               // 58
  callback = (function(callback) {                                                 // 59
    return function(error, response) {                                             // 60
      if (error && response)                                                       // 61
        error.response = response;                                                 // 62
      callback(error, response);                                                   // 63
    };                                                                             // 64
  })(callback);                                                                    // 65
                                                                                   // 66
  // safety belt: only call the callback once.                                     // 67
  callback = _.once(callback);                                                     // 68
                                                                                   // 69
                                                                                   // 70
  ////////// Kickoff! //////////                                                   // 71
                                                                                   // 72
  // from this point on, errors are because of something remote, not               // 73
  // something we should check in advance. Turn exceptions into error              // 74
  // results.                                                                      // 75
  try {                                                                            // 76
    // setup XHR object                                                            // 77
    var xhr;                                                                       // 78
    if (typeof XMLHttpRequest !== "undefined")                                     // 79
      xhr = new XMLHttpRequest();                                                  // 80
    else if (typeof ActiveXObject !== "undefined")                                 // 81
      xhr = new ActiveXObject("Microsoft.XMLHttp"); // IE6                         // 82
    else                                                                           // 83
      throw new Error("Can't create XMLHttpRequest"); // ???                       // 84
                                                                                   // 85
    xhr.open(method, url, true, username, password);                               // 86
                                                                                   // 87
    // support custom "ejson-binary" response type                                 // 88
    // and all browser-supported types                                             // 89
    var convertToBinary;                                                           // 90
    if (options.responseType === "ejson-binary") {                                 // 91
      xhr.responseType = "arraybuffer";                                            // 92
      convertToBinary = true;                                                      // 93
    } else {                                                                       // 94
      xhr.responseType = options.responseType;                                     // 95
    }                                                                              // 96
                                                                                   // 97
    for (var k in headers)                                                         // 98
      xhr.setRequestHeader(k, headers[k]);                                         // 99
                                                                                   // 100
                                                                                   // 101
    // setup timeout                                                               // 102
    var timed_out = false;                                                         // 103
    var timer;                                                                     // 104
    if (options.timeout) {                                                         // 105
      timer = Meteor.setTimeout(function() {                                       // 106
        timed_out = true;                                                          // 107
        xhr.abort();                                                               // 108
      }, options.timeout);                                                         // 109
    };                                                                             // 110
                                                                                   // 111
    // callback on complete                                                        // 112
    xhr.onreadystatechange = function(evt) {                                       // 113
      if (xhr.readyState === 4) { // COMPLETE                                      // 114
        if (timer)                                                                 // 115
          Meteor.clearTimeout(timer);                                              // 116
                                                                                   // 117
        if (timed_out) {                                                           // 118
          callback(new Error("timeout"));                                          // 119
        } else if (! xhr.status) {                                                 // 120
          // no HTTP response                                                      // 121
          callback(new Error("network"));                                          // 122
        } else {                                                                   // 123
                                                                                   // 124
          var response = {};                                                       // 125
          response.statusCode = xhr.status;                                        // 126
                                                                                   // 127
          var body = xhr.response || xhr.responseText;                             // 128
                                                                                   // 129
          // Some browsers don't yet support "json" responseType,                  // 130
          // but we can replicate it                                               // 131
          if (options.responseType === "json" && typeof body === "string") {       // 132
            try {                                                                  // 133
              body = JSON.parse(body);                                             // 134
            } catch (err) {                                                        // 135
              body = null;                                                         // 136
            }                                                                      // 137
          }                                                                        // 138
                                                                                   // 139
          // Add support for a custom responseType: "ejson-binary"                 // 140
          if (convertToBinary && typeof ArrayBuffer !== "undefined" && typeof Uint8Array !== "undefined" && body instanceof ArrayBuffer) {
            var view = new Uint8Array(body);                                       // 142
            var len = body.byteLength;                                             // 143
            var binaryBody = EJSON.newBinary(len);                                 // 144
            for (var i = 0; i < len; i++) {                                        // 145
              binaryBody[i] = view[i];                                             // 146
            }                                                                      // 147
            body = binaryBody;                                                     // 148
          }                                                                        // 149
                                                                                   // 150
          response.content = body;                                                 // 151
                                                                                   // 152
          response.headers = {};                                                   // 153
          var header_str = xhr.getAllResponseHeaders();                            // 154
                                                                                   // 155
          // https://github.com/meteor/meteor/issues/553                           // 156
          //                                                                       // 157
          // In Firefox there is a weird issue, sometimes                          // 158
          // getAllResponseHeaders returns the empty string, but                   // 159
          // getResponseHeader returns correct results. Possibly this              // 160
          // issue:                                                                // 161
          // https://bugzilla.mozilla.org/show_bug.cgi?id=608735                   // 162
          //                                                                       // 163
          // If this happens we can't get a full list of headers, but              // 164
          // at least get content-type so our JSON decoding happens                // 165
          // correctly. In theory, we could try and rescue more header             // 166
          // values with a list of common headers, but content-type is             // 167
          // the only vital one for now.                                           // 168
          if ("" === header_str && xhr.getResponseHeader("content-type"))          // 169
            header_str =                                                           // 170
            "content-type: " + xhr.getResponseHeader("content-type");              // 171
                                                                                   // 172
          var headers_raw = header_str.split(/\r?\n/);                             // 173
          _.each(headers_raw, function (h) {                                       // 174
            var m = /^(.*?):(?:\s+)(.*)$/.exec(h);                                 // 175
            if (m && m.length === 3)                                               // 176
              response.headers[m[1].toLowerCase()] = m[2];                         // 177
          });                                                                      // 178
                                                                                   // 179
          populateData(response);                                                  // 180
                                                                                   // 181
          var error = null;                                                        // 182
          if (response.statusCode >= 400)                                          // 183
            error = makeErrorByStatus(response.statusCode, response.content);      // 184
                                                                                   // 185
          callback(error, response);                                               // 186
        }                                                                          // 187
      }                                                                            // 188
    };                                                                             // 189
                                                                                   // 190
    // send it on its way                                                          // 191
    xhr.send(content);                                                             // 192
                                                                                   // 193
  } catch (err) {                                                                  // 194
    callback(err);                                                                 // 195
  }                                                                                // 196
                                                                                   // 197
};                                                                                 // 198
                                                                                   // 199
/////////////////////////////////////////////////////////////////////////////////////

}).call(this);
